body {
  padding: 0;
  margin: 0;
  width: 100%;
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 22px;
  text-align: center;
  background-color: #ffffff;
  color: #867a74; }

*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.btn {
  display: inline-block;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  text-decoration: none;
  color: #562f2f;
  cursor: pointer;
  border: none;
  outline: 0; }

.header {
  padding: 10px;
  background-color: #fff0e9; }

.header__container {
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 20px; }

.header__wrapper {
  display: flex;
  align-items: center; }

.header__call {
  width: 190px;
  text-align: left;
  padding-left: 30px; }

.header__phone {
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  color: #391111;
  text-decoration: none; }

.header__basket {
  width: 50px; }

.header__basket a {
  position: relative;
  margin-left: auto;
  padding-left: 10px;
  font-size: 10px;
  color: #a74242;
  text-decoration: none; }

.header__basket a span {
  position: absolute;
  right: -5px;
  bottom: -2px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: #ffffff; }

.header__svg {
  fill: #a21418; }

.navigation {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  background-color: #fff0e9; }

.navigation__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  width: auto; }

.navigation__link {
  padding: 5px 42px 10px 0;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #867a74; }
  .navigation__link:hover, .navigation__link:focus {
    color: #342f2d; }

.navigation__address {
  padding: 0;
  margin: 0;
  list-style: none;
  margin-right: auto;
  margin-left: auto;
  width: auto; }

.navigation__svg {
  fill: #a21418; }

.social {
  padding: 0;
  margin: 0;
  list-style: none;
  width: auto;
  display: flex;
  margin-left: auto; }

.social__item {
  margin: 0 5px; }

.social__link svg {
  fill: #a21418; }

.search {
  display: flex;
  min-width: 480px;
  margin: 10px auto;
  padding: 3px;
  background-color: #ffffff;
  box-shadow: inset 0 1px 2px rgba(57, 17, 17, 0.15); }

.search__input {
  padding: 10px 15px;
  flex-grow: 1;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 1.15;
  color: #a74242;
  background-color: #ffffff;
  border: 0; }
  .search__input::placeholder {
    font-size: 16px;
    color: rgba(167, 66, 66, 0.6); }

.search__btn {
  border: 0;
  background-color: transparent; }

.sort {
  max-width: 1210px;
  margin: 0 auto;
  padding: 15px; }

.sort__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.sort__item {
  flex-grow: 1;
  margin: 5px; }

.sort__link {
  display: block;
  padding: 15px 13px;
  font-weight: bold;
  color: #70635d;
  background-color: #ffefe9;
  text-align: center;
  cursor: pointer; }
  .sort__link:hover, .sort__link:focus {
    color: #ffffff;
    background-color: #391111; }

.filter {
  padding: 0;
  margin: 0;
  position: static;
  width: 320px;
  margin-right: 15px;
  background-color: #ffefe9;
  border: 1px solid #f7d3c6;
  z-index: 3; }

.filter__form {
  position: sticky;
  top: 0; }

.filter__title {
  padding: 0;
  margin: 0;
  padding: 15px 0;
  font-family: "Times New Roman", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: 0.1em;
  text-align: center;
  text-transform: uppercase;
  color: #562f2f; }

.filter__nav {
  margin-bottom: 15px; }

.filter__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start; }

.filter__item {
  width: 100%; }

.filter__link {
  padding: 0;
  margin: 0;
  display: block;
  font-size: 14px;
  font-weight: 600;
  line-height: 26px;
  text-align: left;
  color: #70635d; }
  .filter__link--offer, .filter__link--carpets {
    padding-left: 14px; }
  .filter__link:hover, .filter__link:focus {
    background-color: #fbd2a4;
    color: #562f2f; }

.filter__svg {
  margin-left: 5px;
  transform: rotate(-90deg); }

.active .filter__link .filter__svg {
  transform: rotate(0deg); }

.submenu {
  display: none; }
  .submenu--showed {
    padding: 0;
    margin: 0;
    list-style: none;
    display: block;
    padding-left: 20px; }

.submenu__item {
  width: 100%;
  padding-left: 10px; }

.submenu__link {
  display: block;
  padding: 0 5px 0 10px;
  font-weight: 400;
  font-size: 12px;
  line-height: 26px;
  text-align: left;
  color: #70635d; }
  .submenu__link:hover, .submenu__link:focus {
    background-color: #fbd2a4;
    color: #562f2f; }

.submenu__svg {
  transform: rotate(-90deg); }

.active .submenu__link .submenu__svg {
  transform: rotate(0deg); }

.catalog__container {
  max-width: 1210px;
  margin: 0 auto;
  padding: 20px;
  display: flex; }

.catalog__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 443px 461px 430px;
  max-width: 870px;
  margin-bottom: 15px; }

.sorting {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #eed6cd; }

.sorting__title {
  padding: 0;
  margin: 0;
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
  color: #70635d; }

.sorting__select {
  margin: 0 10px;
  padding: 5px;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  color: #562f2f;
  background-color: rgba(134, 122, 116, 0.1);
  border: 1px solid #eed6cd;
  cursor: pointer; }

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding: 15px;
  background-color: #ffffff;
  border: 1px solid #eed6cd; }

.card__link {
  display: block;
  min-height: 200px; }
  .card__link--sixth {
    padding-bottom: 60px; }

.card__title {
  padding: 0;
  margin: 0;
  padding-bottom: 12px;
  font-weight: 700;
  font-size: 20px;
  line-height: 28px;
  text-align: center;
  color: #562f2f; }
  .card__title--second, .card__title--third {
    padding: 0; }

.card__material {
  padding: 0;
  margin: 0;
  padding-bottom: 12px;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  color: #9e968e; }

.card__price {
  padding: 0;
  margin: 0;
  padding-bottom: 12px;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  text-align: center;
  color: #562f2f; }
  .card__price--first {
    padding-bottom: 30px; }

.card-btn {
  margin-top: auto;
  padding: 10px 20px;
  font-weight: 600;
  line-height: 22px;
  color: #ffffffc9;
  background-color: #5b1717;
  border-radius: 50px;
  cursor: pointer; }

.show-more__btn {
  width: 100%;
  padding: 15px;
  background-color: #fbd2a4;
  border: 1px solid #eed6cd; }
  .show-more__btn:hover, .show-more__btn:focus {
    background-color: #f9ba73; }

.footer {
  padding: 20px;
  background-color: #391111; }

.footer__box--production, .footer__box--store, .footer__box--order {
  margin-right: 30px; }

.footer__box--information {
  display: flex;
