  align-items: flex-start; }

.footer__wrapper {
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.footer__title {
  padding: 0;
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0.1em;
  text-align: left;
  text-transform: uppercase;
  color: #fbede6; }
  .footer__title--information {
