.footer__phone {
  padding-left: 20px;
  margin: 5px 0;
  font-size: 16px;
  line-height: 22px;
  color: rgba(251, 237, 230, 0.7); }
  .footer__phone:hover, .footer__phone:focus {
    color: #fbede6; }

.footer__mail {
  padding-left: 20px;
  margin: 5px 0;
  font-size: 16px;
  line-height: 22px;
  color: rgba(251, 237, 230, 0.7); }
  .footer__mail:hover, .footer__mail:focus {
